.seller-form-container {
  width: 70% !important;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  gap: 20px;
}

.form-routes {
  display: flex;
  flex-direction: column;
  gap: 40px;
  position: relative;
  z-index: 50;
  flex: 1 0 25%;
}
.form-routes.vehicledetail::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 50%;
  background-color: #050f56;
  top: 30%;
  left: 13%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.form-routes .route-nav {
  display: flex;
  align-items: center;
  gap: 20px;
}

.form-routes .route-nav a {
  text-decoration: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: #929292;
}

.form-routes .route-nav a.active {
  text-decoration: underline;
  color: #00adef;
}

.form-routes .route-nav .circle {
  width: 60px;
  height: 60px;
  background: #ffffff;
  border: 0.5px solid #ebebeb;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 50%;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.form-routes .route-nav .circle.active {
  border: 2px solid #00adef;
}

@media screen and (max-width: 1000px) {
  /* Vehicle Detail Container */
  .vehicle-detail-container {
    width: 140% !important;
    margin-left: 3% !important;
    background: #ffffff;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }

  .vehicle-detail-container .vehicle-detail-title {
    display: none !important;
    justify-content: center;
    align-items: center;
    padding: 10px 0 0;
  }
  .vehicle-detail-container .vehicle-detail-title h5 {
    font-family: "Poppins";
    font-weight: bold;
    font-size: 20px;
    color: #050f56;
  }

  .vehicle-detail-container .container-wrapper {
    width: 100% !important;
    padding: 20px;
  }

  .row-one {
    display: flex;
    align-items: center;
    gap: 20px !important ;
    flex-wrap: wrap !important;
  }
  .row-one .vehicle-document {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    width: 45% !important;
    /* background-color: orange; */
  }
  .row-one .vehicle-document h6 {
    font-family: "Poppins";
    font-weight: 500;
    font-size: 14px !important;
    color: #050f56;
  }
  .row-one .vehicle-document .document {
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 40px;

    width: 85% !important;
    height: 10%;
  }
  .row-one .vehicle-document .document img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  /* Row Two */
  .row-two {
    margin-left: 1.5%;
    margin-top: 20px;
  }
  .row-two .vehicle-documents {
    display: flex;
    align-items: flex-start;
    gap: 40px !important;
    flex-wrap: wrap !important;
  }
  .row-two h6 {
    font-family: "Poppins";
    font-weight: 500;
    font-size: 14px !important;
    color: #050f56;
  }
  .row-two .vehicle-documents .document {
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 40px;
    cursor: pointer;
    width: 40% !important;
    height: 10%;
  }

  .row-two .vehicle-documents .document img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  /* Row Three */

  .row-three {
    margin-top: 20px;
    margin-left: 1.5%;
  }
  .row-three .vehicle-documents {
    display: flex;
    align-items: flex-start;
    gap: 40px !important;
  }
  .row-three h6 {
    font-family: "Poppins";
    font-weight: 500;
    font-size: 14px !important;
    color: #050f56;
  }
  .row-three .vehicle-documents .document {
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 40px;
    cursor: pointer;
    width: 40% !important;
    height: 10%;
  }

  .row-three .vehicle-documents .document img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .continue-btn {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    gap: 1rem;
    align-items: center;
  }
  .continue-btn button {
    border: none;
    padding: 5px 20px !important;
    background: #050f56;
    border: 1px solid #c4c4c4;
    border-radius: 10px;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 15px !important;
    font-family: "Roboto";
    width: 40% !important;
    text-align: center;
  }
}

